@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One&display=swap');
.home{
    color: white;
    overflow-x: hidden;
}

.container{
    position: relative;
    z-index: -3;
    max-width: 1140px;
    margin: 0 auto;
    margin-top: 100vh;
    margin-bottom: 30%;
}

.container .content-header{
    align-items: center;
    

}

.content-header{
    color: white;
    text-align: center !important;
    text-transform: uppercase;
}

.content-header h1{
    font-size: 150px;
    font-weight: 500;
    font-family: 'Black Ops One';
    color: white;
    margin-bottom: -40px;
    text-shadow: 0 0 12px rgb(0, 0, 0), 0 0 20px rgb(0, 191, 255), 0 0 100px purple, 0 0 50px rgb(0, 187, 255);
    
}

.content-header h2{
    font-size: 40px;
    font-weight: 500;
    font-family: 'Black Ops One';
    color: white;
}

content {
    width: 100px;
    height: auto;
    margin-right: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.paralax{
    position: absolute;
    height: 100%;
}

.paralax-item{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;    
}

.p-item1{
    background: url("../../../public/starry.png") center/cover no-repeat;
    z-index: -3;
    
}

.p-item3{
    background: url("../../../public/scroll1.png") bottom/100% no-repeat;
    background-size: cover;
    background-position: center;
    z-index: -2;

}

.p-item4{
    position: absolute;
    top: 0;
    left: 0;
    width: 10000%;
    height: 100%;
    content: '';
    background: url("../../../public/fog.png");
    background-size: contain;
    background-position: center;
    animation: marquee 20s linear infinite;
}

.p-item4::before{
    position: absolute;
    top: 0;
    left: 0;
    width: 1000%;
    height: 100%;
    content: '';
    background: url("../../../public/fog.png");
    background-size: contain;
    background-position: center;
    animation: marque 20s linear infinite;
    z-index: 0;

}

@keyframes marquee{
    0%{
        transform: translate3d(0,0,0);
    }

    100%{
        transform: translate3d(-200vw,0,0);
    }
}

.gamericon{
    margin-top: 30%;
    width: 50%;
    
}


.imgdiv{
    display: flex;
}


.content-txt{
    /* padding-top: 40%; */
    margin-top: 30%;
    width: 800px;
    height: 250px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.463);
    border: 1px solid #fff;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    z-index:0;
}

.content-txt h3{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px;
}

.content-txt:before{
    content: '';
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: rgba(0, 0, 0, 0.5);
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    z-index: 3;
}

.gmtitle{
    display: flex;
    align-items: center;
    justify-content: center;
}

.gmtitle h2{
    font-size: 120px;
    font-weight: 500;
    font-family: 'Black Ops One';
    text-shadow: 0 0 12px rgb(0, 0, 0), 0 0 20px rgb(0, 191, 255), 0 0 100px purple, 0 0 50px rgb(0, 187, 255); 
}

.gamecards{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    
}

.card{
    margin-bottom: 30%;
    margin: 5%;
    width: 18rem;
    height: 32rem;
    background: rgb(255, 255, 255);
    border-radius: 20px;
    text-align: center;
    z-index: 28;
}

.cardimg{
    width:100%;
    height: 56.2%;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
}
.card-body{
    color: black;
}

.card-title{
    font-size: large;
    margin-bottom: 0;
}

.card-text{
    font-size: 13px;
}

.card-texts{
    font-size: 13px;
    margin-bottom: 10%;
}

.card:hover{
    background-color: rgb(249, 170, 0);
    transform: scale(1.2);
}
.card1{
    margin-bottom: 30%;
    margin: 5%;
    width: 18rem;
    height: 32rem;
    background: rgb(255, 255, 255);
    border-radius: 20px;
    text-align: center;
    z-index: 28;
}



.overlay{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.8);
    transition: opacity 500ms;
    visibility: hidden;
    opacity: 0;
}

.overlay:target{
    visibility: visible;
    opacity: 1;    
}

.wrapper1{
    margin: 70px auto;
    padding: 20px;
    background: #e7e7e7;
    border-radius: 5px;
    width: 30%;
    position: relative;
    transition: all 5s ease-in-out;     
}

.wrapper1 h2{
    margin-top: 0;
    color: #333;   
}

.wrapper1 .close{
    position: absolute;
    top: 20px;
    right: 30px;
    transition: all 200ms;
    font-size: 30px;
    font-weight: bold;
    text-decoration: none;
    color: #333;
}

.wrapper1 .content1{
    max-height: 100%;
    overflow: auto;  
}

.container1{
    border-radius: 5px;
    background-color: #e7e7e7;
    padding: 20px 0;
}

form label{
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 3px;
}

input[type=text], textarea{
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
    margin-top: 6px;
    margin-bottom: 16px;
    /* resize: vertical;      */
}

input[type="submit"]{
    background-color: #413b3b;
    color: #fff;
    padding: 15px 50px;
    border: none;
    border-radius: 50px;
    cursor: pointer;
    font-size: 15px;
    text-transform: uppercase;
    letter-spacing: 3px;
}

.card-btn{
    background-color: #413b3b;
    color: #fff;
    padding: 10px 15px;
    border: none;
    border-radius: 50px;
    cursor: pointer;
    font-size: 15px;
    text-transform: uppercase;
    letter-spacing: 3px;
    text-decoration: none;
    
}

.button1{
    background-color: #413b3b;
    color: #ffffff;
    padding: 10px 15px;
    border: none;
    border-radius: 50px;
    cursor: pointer;
    font-size: 15px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 3px;
    text-decoration: none;
    
}